import BScroll from 'better-scroll';
import { MessageBox } from 'mint-ui';
import { Toast } from 'mint-ui';
export default {
  data(){
    return {
      pageTitle:'',
      userData:{
        username:localStorage.CDECY_USERNAME,
        nickName:localStorage.CDECY_NICKNAME,
        portrait:localStorage.CDECY_PORTRAIT,
        userGender:localStorage.CDECY_USERGENDER,
        userAge:localStorage.CDECY_USERAGE,
      }
    }
  },
  //
  components: {


  },
  //computed
  computed:{

  },
  //mounted function 
  mounted(){
    // if(document.documentElement&&document.documentElement.scrollTop)
    //   {
    //     document.documentElement.scrollTop=0;
    //   }
    //   else if(document.body)
    //   {
    //     document.body.scrollTop=0;
    //   };
      
  },
  // 
  methods:{
    //
    exit(){
      MessageBox.confirm('?').then(action => {
        if(action=="confirm"){
          Bmob.User.logOut();
          localStorage.CDECY_NICKNAME="";     //
          localStorage.CDECY_USERNAME="";     //
          localStorage.CDECY_PORTRAIT="";     //
          localStorage.CDECY_USERGENDER="";   //
          localStorage.CDECY_USERAGE="";   //
          localStorage.CDECY_REMINDID="";
          localStorage.CDECY_ACCID="";
          this.$router.push("/login")
        }
      });
    },
    //
    toDynamic(){
      this.$router.push("/dynamic")
    },
    //
    zwkf(){
      Toast(
        {
          message: '()',
          position: 'center',
          duration: 2000
        }
      );
    },

  }
}



